import React from 'react'
import '../CssFolder/navbar.css'
import { Link } from 'react-router-dom'
import CallIcon from '@material-ui/icons/Call';

function NavBar() {

    const makeCall=()=>{
        window.open('tel:7387191410')
    }

    return (
        <div className="navBardviv">
           
            <p  className="nav_title">CraftMania</p>
            <CallIcon onClick={makeCall} className="nav_call"/>
          
        </div>
    )
}

export default NavBar
