import React from 'react'
import 'react-slideshow-image/dist/styles.css'
import { Slide } from 'react-slideshow-image';
import '../CssFolder/imageslider.css'

function ImageSlider() {

    const slideImages = [
        'https://static.parenting.com/wp-content/uploads/2019/07/31151205/Personalized-School-Supplies-1200x720.jpg',
        'https://cdn.thewirecutter.com/wp-content/uploads/2020/05/kidscrafts-lowres-2x1-8972-1024x512.jpg',
        'https://hips.hearstapps.com/hmg-prod.s3.amazonaws.com/images/summer-crafts-1590677840.jpg',
        'https://hips.hearstapps.com/hmg-prod.s3.amazonaws.com/images/back-to-school-diy-1532979234.jpg'
      ];

      return (
        <div className="slide-container">
          <Slide transitionDuration="500" indicators={true}>
            <div className="each-slide">
              <div className="s_img" style={{'backgroundImage': `url(${slideImages[0]})`}}>
                <span></span>
              </div>
            </div>
            <div className="each-slide">
              <div className="s_img" style={{'backgroundImage': `url(${slideImages[1]})`}}>
                <span></span>
              </div>
            </div>
            <div className="each-slide">
              <div className="s_img" style={{'backgroundImage': `url(${slideImages[2]})`}}>
                <span></span>
              </div>
            </div>
            <div className="each-slide">
              <div className="s_img" style={{'backgroundImage': `url(${slideImages[3]})`}}>
                <span></span>
              </div>
            </div>
          </Slide>
        </div>
      )
}

export default ImageSlider
